@use "utils/style/mixins.scss";
@use "utils/style/variables.scss";

.economy {
  display: flex;
  flex-direction: column;
  &__pannel{
    @include mixins.pannel;
  }
  &__title {
    margin: 0;
    padding: 0;
    background-color: variables.$primary-color-light;
    font-size: 25px;
  }
  &__subtitle{
    margin-top: 10px;
    font-size: 22px;
    background-color: variables.$primary-color-light;
  }
  &__text {
    &--red {
      color: red;
      font-size: 20px;
    }
  }

  &__buttons{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 20px;
    margin-top: 10px;
  }
}
